.header__appbar {
  max-width: 100vw;
  max-height: 89px;
  background-color: #000;
  border-bottom: solid #111111;
  overflow: hidden;
}

.header__appbar--sticky {
  position: fixed;
  opacity: .8;
  top: 0;
  left: 0;
  width: 100vw;
}

  .header__appbar > li {
    float: left;
    list-style: none;
  }

  .header__appbar li:first-child {
    margin-left: 20px;
  }


  .header__appbar > li:last-child {
    float: right;
    margin-right: 20px;
  }

  .header__appbar > li:last-child::after {
    clear: both;
  }


    .appbar__item {
      display: block;
      padding: 25px;
      text-decoration: none;
      color: #818181;
      font-size: 18px;
      -webkit-transition: .3s ease-in-out;
      transition: .3s ease-in-out;
    }

    .appbar__item:hover {
      color: #f1f1f1;
    }
    
@media (max-width: 720px) {
  .header__appbar {
    display: none;
  }
}

.sidebar {
  width: 100%;
  height: 100%;
  background-color: #0e0e0e;
  position: fixed;
  top: 0;
  margin-left: -100%;
  z-index: 1000;
  overflow-x: hidden;
  padding-top: 60px;
  -webkit-transition: .3s ease-in-out;
  transition: .3s ease-in-out;
}


.sidebar.sidebar--expanded {
  margin-left: 0;
}

.sidebar li {
  list-style: none;
  padding: 8px 8px 8px 75px;
}

.sidebar li a {
  text-decoration: none;
  font-size: 25px;
  color: #818181;
  display: block;
  -webkit-transition: .3s ease-in-out;
  transition: .3s ease-in-out;
}

.sidebar a:hover {
  color: #f1f1f1;
}

.sidebar i {
  font-size: 48px;
  position: relative;
  color: #F57F17;
  border-radius: 0 !important;
  margin-bottom: 18px;
  cursor: pointer;
}

.sidebar__menuIcon {
  background-color: #000 ;
  padding: 25px ;
  width: 100%;
  height: 18px;
}
.menuIcon__item {
  height: 3px;
  background-color: white;
  width: 25px;
  position: relative;
}

.menuIcon__item2 {
  top: 5px;
}

.menuIcon__item3 {
  top: 10px;
}



