@media screen and (max-width: 720px) {

  .header {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    max-width: 100vw;
  }
  
  .header__img {
    -webkit-transition: .5s ease-in-out;
    transition: .5s ease-in-out;
    max-width: 100%;
  }
  
  .header__flex1 {
    display: none;
  }

  .header__img5 {
    display: none;
  }
  .header__img6 {
    display: none;
  }
  
  .header__img4 {
    flex: 1 1;
    background-image: url(/static/media/img3mini.c61496d8.JPG);
    background-size: cover;
    background-repeat: no-repeat;
    max-width: 100%;
    height: auto;
    -webkit-filter: grayscale(0);
